.slideshow-container {
  width: 100%;
  max-width: 500px;
  height: 300px;
  margin: 20px auto;
  overflow: hidden;
  border-radius: 20px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
}

.slideshow-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity 1s ease-in-out;
}
